.App {
  text-align: center;
  background-color: aquamarine;
}

body{
  display:  flex;
  align-items: center;
  justify-content: center;
  height: 100vh;

}
.buttonBox{

  border: 1px black solid;
  border-radius: 5px;
  height: 80px;
  width: 50px;
 
}

.buttonwrap{
display: flex;
align-items: center;
justify-content: center;
flex-wrap: wrap;
width: 200px;

}

.buttonBox:hover{
  background-color:pink;
}

.display{
  background-color: rgb(71, 118, 158);
  border: 1px solid black;
  border-radius:  10px;
}